"use client";

import { EnumFormulaType } from "@/gateway/formula";
// import { Button, Card, CardFooter, Tooltip } from '@nextui-org/react';
import useDeviceContext from "@/hooks/useDeviceContext";
import { LineChart } from "@buildo/bento-design-system";
import { useEffect, useRef, useState } from "react";
import { FiMove } from "react-icons/fi";
import { GiCoffeeBeans } from "react-icons/gi";
import { IoIosCloseCircle, IoIosLeaf } from "react-icons/io";
import { MdCoffeeMaker } from "react-icons/md";
import { PiDotsThreeCircleVerticalThin } from "react-icons/pi";
import { FaStopCircle } from "react-icons/fa";
import { MovingBorder, Button } from "./ui/moving-border";
import { cn } from "@/util";
import { motion } from 'framer-motion'

export default function ProfileCard({
  formulaId,
  onClickEdit,
  onCancel,
  onClick,
  onDelete ,
}: {
  formulaId: string;
  onCancel: () => void;
    onClick: () => void;
  
    onClickEdit: () => void;
    onDelete: () => void;
}) {
  //https://github.com/ccnmtl/juxtapose/blob/master/src/TrackElement.jsx#L83

  const { draggable, progressReport, formulaFeedbacks, selectedFormulaId } = useDeviceContext();
  const [height, setHeight] = useState(0);
  const elementRef = useRef<HTMLDivElement>(null);

  const { formulas, setFormulas, formulaUpdatedTimestamp } = useDeviceContext();


  useEffect(() => {
    if (elementRef.current) {
      setHeight(elementRef.current.clientHeight);
    }
  }, []);

  const [isRunning, setRunning] = useState(false);
  const timeoutRef = useRef<ReturnType<typeof setTimeout>>();
  const [formulaStartTimestamp, setFormulaStartTimestamp] = useState<number>(0);



  useEffect(() => {

    const elapsed = new Date().getTime() - (formulaUpdatedTimestamp[formulaId] || 0)
    // console.log("Update/  updated", { formulaUpdatedTimestamp, formulaId, elapsed })
    if (elapsed < 2000) {
      // if recently updated, show progress
      if (!isRunning) {
        setFormulaStartTimestamp(new Date().getTime());
      }
      setRunning(true)

      clearTimeout(timeoutRef.current)
      timeoutRef.current = setTimeout(() => {
        setRunning(false)
      }, 2000)

    }

  }, [formulaUpdatedTimestamp, formulaId, isRunning])

  const normalView = (
    <>

      <div className={cn(
        "absolute left-0 top-0 flex flex-col items-center  h-full p-1 ",
      )}>
        {draggable ? (
          <div className={cn(
            "bg-white rounded-xl divide-y-1 divide-gray-100 flex flex-col h-full ",
            draggable ? "justify-evenly opacity-30" : "justify-start "

          )}>
            <button
              className="flex justify-center"
              onMouseDown={(e) => {
                onClickEdit();
                e.preventDefault();
                e.stopPropagation();
              }}
              onTouchStart={(e) => {
                onClickEdit();
                e.preventDefault();
                e.stopPropagation();
              }}
            >
              <PiDotsThreeCircleVerticalThin
                size={32}
                className=" text-black "
              />
            </button>

            <button className="flex justify-center">
              <FiMove size={20} className="drag-handle m-1 text-black" />
            </button>
            <button
              className="flex justify-center"
              onMouseDown={(e) => {
                onDelete();
                e.preventDefault();
                e.stopPropagation();
              }}
              onTouchStart={(e) => {
                onDelete();
                e.preventDefault();
                e.stopPropagation();
              }}

            
            >
              <IoIosCloseCircle size={25} className="drag-handle text-black" />
            </button>

          </div>
        ) : (
          <>
            {isRunning ? (
              <div className="m-1">
                <FaStopCircle size={32} className="m-1 text-white" />
              </div>
            ) : (
              <div className="opacity-30">
                {formulas[formulaId]?.formulaType ===
                  EnumFormulaType.Espresso && (
                    <GiCoffeeBeans size={32} className="m-1 text-white" />
                  )}
                {formulas[formulaId]?.formulaType ===
                  EnumFormulaType.Teapresso && (
                    <IoIosLeaf size={32} className="m-1 text-white" />
                  )}
                {formulas[formulaId]?.formulaType ===
                  EnumFormulaType.PourOver && (
                    <MdCoffeeMaker size={32} className="m-1 text-white" />
                  )}

              </div>
            )}


          </>
        )}
      </div>
      <div className="">
        <h1 className={cn(
          "select-none text-left text-md text-white m-2 font-mono break-all font-bold",
          draggable ? "opacity-20 ml-10": 'opacity-100'
        )}>
          {isRunning ? (
            String(Math.round((new Date().getTime() - formulaStartTimestamp) / 1000 * 10) / 10) + " s"
          ) : (formulas[formulaId]?.title || "")}
        </h1>
      </div>
    </>
  );

  const progressView = (
    <div className="first-letter absolute bottom-0  left-0 h-full w-full">
      <LineChart
        height={height}
        dataKey="name"
        categories={["flowrate"]}
        hideLegend={true}
        hideXAxis={true}
        hideYAxis={true}
        data={[
          ...Array.from({ length: 10 }, (_, index) => ({
            name: `Point ${index + 1}`,
            flowrate: Math.floor(Math.random() * 100),
          })),
        ]}
      />{" "}
    </div>
  );


  return (
    <motion.div
      className="w-full h-full "
      animate={{
        opacity: 1,
        scale: 1
      }}
      initial={{
        opacity: 0,
        scale: 0
      }}
      whileHover={{
        scale: 0.99
      }}
      transition={{
        delay: Math.random() * 1,
        type: "spring",
        stiffness: 100,
      }}


    >
      <Button
        // duration={3333}
        borderRadius="16px"
        isRunning={isRunning}
        ref={elementRef}
        // isFooterBlurred
        // radius="lg"
        onMouseDown={(e) => {
          console.log("profile/ clicked");
          if (isRunning) {
            onCancel()
          }
          else {
            onClick()
          }
        }}
        backgroundColor={formulas[formulaId]?.color || "black"}

        className={
          cn(
            "border flex h-full w-full transform flex-col items-center justify-center shadow-large cursor-pointer select-none",
            // formulas[formulaId]?.backdrop,

            isRunning ? " border-black" : `border-white`,
            isRunning ? "animate-none" : "animate-none",

          )}

      >
        {normalView}
      </Button>


    </motion.div>

  );
}
