function HomePage() {
  // const [layout, setLayout] = useState<Layouts>({});
  const [isLoaded, setLoaded] = useState(false);
  const { formulas, setFormulas, addFormulaOpen, setAddFormulaOpen } =
    useDeviceContext();
  const { layout, setLayout } = useDeviceContext();

  const [deleteModal, setDeleteModal] = useState(false);
  const [stagingFormulaId, setStagingFormulaId] = useState('');
  const router = useRouter();
  const { showToast } = useToast();

  const { registerFormulaClick, registerSelectedFormulaId } =
    useDeviceContext();

  async function addFormula() {
    const formulaId = uuid();
    var stagingSteps = []
    var statingTitle = ''
    var stagingBackdrop = ''

    // if staging formula name are list of number, then it's a share token
    if (stagingFormulaName.match(/^[0-9]+$/) != null) {
      // DEPRECATED
      // it's a share token
      const request = await fetch(`https://api.anatalab.com/gateway/rosario/get_token/${stagingFormulaName}`)

      const body = await request.json()
      console.log("Formula is", body.formulas)

      setStagingFormulaName(body.formulas.title)
      setFormulaType(body.formulas.formulaType)
      stagingSteps = body.formulas.steps
      statingTitle = body.formulas.title
      stagingBackdrop = body.formulas.backdrop
    }
    else {
      statingTitle = stagingFormulaName
    }




    function placeRandomToLayout(layout: RGLayout[], size: string): RGLayout {
      // place it to nearest Line
      // first, decide the random size, width height either be 1 or 2
      const width = Math.random() > 0.5 ? 1 : 2
      const height = Math.random() > 0.5 ? 1 : 2
      const maxCols = GRID_SIZE[size]

      // now read the layout, layout is an array of object, each object has x, y location and w, h as width and height, we need to find the nearest location that we can fit
      // for (var row = 0; row < 20; row++){
      //   for (var col = 0; col < GRID_SIZE[])
      // }

      // generate a mapping boolean first
      var map: boolean[][] = []
      for (var row = 0; row < 20; row++) {
        for (var col = 0; col < maxCols; col++) {
          map[row][col] = false
        }
      }
      // map each current layout to map, also aware that each have width and height
      layout.forEach((layoutItem) => {
        for (var row = layoutItem.y; row < layoutItem.y + layoutItem.h; row++) {
          for (var col = layoutItem.x; col < layoutItem.x + layoutItem.w; col++) {
            map[row][col] = true
          }
        }
      })




      var candidateX, candidateY;
      for (var row = 0; row < 20; row++) {
        for (var col = 0; col < maxCols; col++) {
          var possible = true;
          // iterate through the layout, if there is any cell that is occupied, then it's not possible
          for (var placementRow = 0; placementRow < height; placementRow++) {
            for (var placementCol = 0; placementCol < width; placementCol++) {
              // now
              if (map[row + placementRow][col + placementCol] == true) {
                possible = false
              }

            }
          }

          if (possible) {
            alert('possible' + JSON.stringify({
              i: formulaId,
              x: candidateX,
              y: candidateY,
              w: width,
              h: height
            }))
            return {
              i: formulaId,
              x: candidateX,
              y: candidateY,
              w: width,
              h: height
            }
          }
        }
      }
      alert()
      return {
        i: formulaId,
        x: candidateX,
        y: candidateY,
        w: width,
        h: height
      }

    }

    setLayout(
      produce(layout, (draft) => {
        // draft["md"]?.push({ i: formulaId, x: 1, y: 1, w: 1, h: 1 });
        // draft["xs"]?.push({ i: formulaId, x: 1, y: 1, w: 1, h: 1 });
        // draft["sm"]?.push({ i: formulaId, x: 1, y: 1, w: 1, h: 1 });

        draft['md']?.push(placeRandomToLayout(layout['md'], 'md'))
        draft['lg']?.push(placeRandomToLayout(layout['lg'], 'lg'))
        draft['sm']?.push(placeRandomToLayout(layout['sm'], 'sm'))
        draft['xs']?.push(placeRandomToLayout(layout['xs'], 'xs'))
        draft['xxs']?.push(placeRandomToLayout(layout['xxs'], 'xxs'))
      }),
    );

    setFormulas(
      produce(formulas, (draft) => {
        let backdrop: string = "bg-white";
        if (formulaType == EnumFormulaType.Espresso) {
          backdrop =
            espressoGradient[
            Math.floor(Math.random() * espressoGradient.length)
            ];
        } else if (formulaType == EnumFormulaType.PourOver) {
          backdrop =
            pourOverGradients[
            Math.floor(Math.random() * pourOverGradients.length)
            ];
        } else if (formulaType == EnumFormulaType.Teapresso) {
          backdrop =
            teaGradients[Math.floor(Math.random() * teaGradients.length)];
        }
        const randomColors = ["#f44336", "#e91e63", "#9c27b0", "#673ab7", "#3f51b5", "#2196f3", "#03a9f4", "#00bcd4", "#009688", "#4caf50", "#8bc34a", "#cddc39", "#ffeb3b", "#ffc107", "#ff9800", "#ff5722", "#795548", "#607d8b"]
        const formula = {
          id: formulaId,
          title: statingTitle,
          text: "New Formula",
          color: randomColors[Math.floor(Math.random() * randomColors.length)],
          backdrop: stagingBackdrop || backdrop,
          formulaType: formulaType,
          // randomGradients[
          //   Math.floor(Math.random() * randomGradients.length)
          // ],
          steps: stagingSteps,
          buildupDuration: 1,
          lossyWeight: 0,
          maximumDuration: 0,
          isShared: false,
          category: ""
        };
        draft[formulaId] = formula;
        console.log("fml/ create new", formula);
      }),
    );

    console.log("addFormula", { layout, formulas });
  }
  function clickAddFormula() {
    setAddFormulaOpen(true);
  }

  const renderedFormulas: React.ReactNode[] = [];
  Object.values(formulas).forEach((fml: DisplayFormulaParam) => {
    const formulaComponent = (
      <div key={fml.id} className="grid-item__title ">
        <div
          className="h-full w-full"
        // https://github.com/react-grid-layout/react-grid-layout/issues/166
        // onClick={() => {
        //   alert("clicked")
        // }}
        >
          <ProfileCard
            formulaId={fml.id}
            onClick={() => {
              registerFormulaClick(fml.id);
            }}
            onCancel={() => {
              cancelFormulaClick(fml.id)
            }}
            onClickEdit={() => {
              registerSelectedFormulaId(fml.id);
              router.push(`/lab`);
            }}
            onDelete={() => {
              setDeleteModal(true)
              setStagingFormulaId(fml.id)
            }}
          />
        </div>
      </div>
    );

    renderedFormulas.push(formulaComponent);
  });

  const { draggable } = useDeviceContext();
  // if (draggable) {
  //     renderedFormulas.push(
  //       <div
  //         key={"add"}
  //         className="flex flex-col grid-item__title  bg-gray-300 cursor-pointer rounded-xl border  justify-center items-center hover:scale-105 transition-transform duration-200 hover:border-blue-500"
  //       >
  //         <div
  //           className="w-full h-full flex items-center justify-center drag-handle"
  //           onMouseDown={() => {
  //             clickAddFormula();
  //           }}
  //           onTouchStart={() => {
  //             clickAddFormula();
  //           }}
  //         >
  //           <FaPlus size={32} className="text-white" />
  //         </div>
  //         <button className="absolute top-0 right-0 drag-handle">
  //           <FiMove size={20} className="text-white m-1 drag-handle" />
  //         </button>
  //       </div>
  //     );

  // }

  const [stagingFormulaName, setStagingFormulaName] = useState("");
  const [formulaType, setFormulaType] = useState<EnumFormulaType>(
    EnumFormulaType.Espresso,
  );
  const { t } = useTranslation();
  return (
    <>
      <Head>
        <title>â˜• ROSARIO</title>
      </Head>
      <Layout>
        <div className="dark:bg-gray h-full overflow-auto mt-4 bg-gray-200 bg-white bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]">
          <ResponsiveGridLayout
            autoSize={true}
            verticalCompact={true}
            compactType={"vertical"}
            className="layout relative"
            layouts={layout}
            isDraggable={draggable}
            isResizable={draggable}
            useCSSTransforms={false}
            // resizeHandles={["se"]}

            draggableHandle=".drag-handle"
            breakpoints={{ lg: 1280, md: 992, sm: 767, xs: 430, xxs: 0 }}
            cols={GRID_SIZE}
            onLayoutChange={(layout: RGLayout[], layouts: Layouts) => {
              console.log("layout.evt", layout, layouts)
              if (!isLoaded) {
                setLoaded(true);
                return;
              }
              // localStorage.setItem("fml-layout", JSON.stringify(layouts));
              setLayout(layouts);
            }}
            onDragStart={() => {
              console.log("OnDragStart");
            }}
          >
            {renderedFormulas}
          </ResponsiveGridLayout>
        </div>

        {deleteModal && (
          <Modal
            kind="destructive"
            errorBannerWidth="fill"
            title="This will delete the formula"
            onClose={() => setDeleteModal(false)}
            primaryAction={{
              label: "OK",
              onPress: () => {
                // delete the formula from formulas
                setFormulas(produce(formulas, (draft) => {
                  delete draft[stagingFormulaId]
                }))


                setDeleteModal(false)
              }
            }}
            secondaryAction={{
              label: "Cancel",
              onPress: () => {
                setDeleteModal(false)
              }
            }}
          >
            <Stack space={4}>
              <h1>Are you sure you want to delete this formula?</h1>
            </Stack>
          </Modal>
        )}


        {addFormulaOpen && (
          <Modal
            title={t("Add Formula")}
            onClose={() => setAddFormulaOpen(false)}
            primaryAction={{
              label: "OK",
              onPress: async () => {
                setAddFormulaOpen(false);
                await addFormula();
              },
            }}
            secondaryAction={{
              label: t("Cancel"),
              onPress: () => setAddFormulaOpen(false),
            }}
          >
            <Stack space={4}>
              <TextField
                label={t("Formula Name or Share Token")}
                value={stagingFormulaName}
                onChange={(e: string) => {
                  setStagingFormulaName(e);
                }}
              />
              <Tiles space={4} columns={3}>
                <Button
                  hierarchy="primary"
                  kind={
                    formulaType == EnumFormulaType.Espresso
                      ? "solid"
                      : "outline"
                  }
                  label={t("Espresso")}
                  onPress={() => {
                    setFormulaType(EnumFormulaType.Espresso);
                  }}
                // icon={() => <GiManualMeatGrinder />}
                />
                <Button
                  hierarchy="primary"
                  kind={
                    formulaType == EnumFormulaType.Teapresso
                      ? "solid"
                      : "outline"
                  }
                  label={t("Teapresso")}
                  onPress={() => {
                    setFormulaType(EnumFormulaType.Teapresso);
                  }}
                // icon={() => <GiManualMeatGrinder />}
                />
                <Button
                  hierarchy="primary"
                  kind={
                    formulaType == EnumFormulaType.PourOver
                      ? "solid"
                      : "outline"
                  }
                  label={t("Pour Over")}
                  onPress={() => {
                    setFormulaType(EnumFormulaType.PourOver);
                  }}
                // icon={() => <GiManualMeatGrinder />}
                />
              </Tiles>
            </Stack>
          </Modal>
        )}
      </Layout>
    </>
  );
}
export default HomePage;
